.users {
  display: flex;
  flex-wrap: wrap;
}

.item {
  width: 35%;
  height: 300px;
  border-radius: 7px;
  margin: 20px;
  background-color: #fff;
  overflow: hidden;
}

.img {
  width: 100%;
  height: 40%;
  background: url(../../img/fon.jpg) no-repeat center center;
  background-size: cover;
  overflow: hidden;
}

.info{
  display: block;
  padding: 0 20px;
}

.avatar{
  height: 80px;
  width: 80px;
  border-radius: 50%;
  border: solid 8px #fff;
  margin-top: -60px;
  float: left;
}

.fullName{
  font-size: 16px;
  font-weight: 600;
  color: #27aae1!important;
}
.userStatus{

}
.userLocation{
}

.btn{
  float: right!important;
  margin: 20px 0;
  color:#8dc63f;
  width: 70px;
/*   outline: none;
  border: 0;
  background: transparent */
}

.btn:hover {
  cursor: pointer;
  text-decoration: underline;
  color: #7bab3a;

}

.pagination{
  display: flex;
  justify-content: center;
  cursor: pointer;
  flex-wrap: wrap;
}

.pageNumber{
  color: black;
  padding: 8px 16px;
}

.selectPage {
  background-color: #4CAF50;
  color: white;
  border-radius: 5px;
}

.pageNumber:hover:not(.selectPage){
  background-color: #ddd;
  border-radius: 5px;
}
